import React from "react"
import { graphql, StaticQuery } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

import "../utils/normalize.css"
import "../utils/css/screen.css"

const AboutPage = ({ data }, location) => {
  const siteTitle = data.site.siteMetadata.title

  return (
    <Layout title={siteTitle}>
      <SEO title="ABout" keywords={[`blog`, `gatsby`, `javascript`, `react`]} />

      <article className="post-content page-template no-image">
        <div className="post-content-body">
          <h3 id="dynamic-styles">About Me</h3>
          <p>
            My name is Nina Shenker-Tauris. I am a bioinformatics master&#39;s student at the Technical University of Denmark (DTU). 
          </p>
          <p>
            I created this website in order to showcase one of my biggest passions - skincare. Take the quiz now to discover the secret to your best skin!
          </p>
          <p>
            Let&#39;s connect on <a href="http://www.linkedin.com/in/nina-shenker"> LinkedIn </a> or follow me on <a href="https://github.com/ninashenker"> GitHub</a>.
          </p>
        </div>
      </article>
    </Layout>
  )
}

const indexQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`

export default props => (
  <StaticQuery
    query={indexQuery}
    render={data => (
      <AboutPage location={props.location} data={data} {...props} />
    )}
  />
)
